
$text-size: calc(10px + 20 * (100vw / 5000))

.App
    text-align: center
    font-size: $text-size

    &-logo
        height: 40vmin
        pointer-events: none

.App-header
    background-color: #282c34
    min-height: 100vh
    display: flex
    flex-direction: column
    align-items: center
    justify-content: center
    font-size: calc(10px + 2vmin)
    color: white

.App-link
    color: #61dafb

.container
    max-width: 1040px
    min-width: 70%
    margin: 0 auto
    word-break: break-word
    padding: 2%

@keyframes App-logo-spin
    from
        transform: rotate(0deg)

    to
        transform: rotate(360deg)
