.modal
    height: 100vh
    width: 100vw
    background-color: rgba(0,0,0,0.4)
    position: fixed
    top: 0
    left: 0
    display: flex
    align-items: center
    justify-content: center
    opacity: 0
    pointer-events: none
    transition: 0.5s
    &.active
        opacity: 1
        pointer-events: all
    &__content
        padding: 20px
        background-color: white
        transform: scale(0.5)
        transition: 0.4s all
        width: 40vw
        &.active
            transform: scale(1)

.cross
    position: absolute
    right: 3%
    top: 2%
    cursor: pointer
    &:hover
        color: green
